  box-sizing: border-box; }

a {
  text-decoration: none; }

ul {
  list-style: none;
  padding: 0; }

h1, h2 {
  margin: 0; }

img {
  width: 100%; }

.body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 24px;
  font-family: 'Lato', 'Oswald', 'Philosopher',  'Pacifico', cursive;
  background-color: #0cc2f0; }

.container {
  max-width: 80%;
  margin: 0 auto; }

.green {
  background-color: green; }

