.m-main__wrap {
  position: relative;
  background-color: #0cc2f0;
  height: 100vh; }

.m-main__btn {
  position: fixed;
  right: 50px;
  top: 60px;
  display: block;
  width: 120px;
  height: 90vh;
  background-color: #0cc2f0;
  text-align: center; }

.topics {
  margin-top: 60px; }
  .topics__list {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    margin: 0; }
    @media (max-width: 1239px) {
      .topics__list {
        padding: 0; } }
  .topics__item {
    padding: 20px 0;
    text-align: center;
    color: #f5f0f4;
    font-weight: bold;
    font-size: 30px;
    background-color: #ad5f06;
    text-shadow: 3px 3px 6px #302f2f;
    letter-spacing: 0.2em;
    border: 1px solid #7a6a6a;
    transition: .5s; }
    .topics__item:hover {
      background-color: #e4e729;
      color: #2c09f5; }
    @media (max-width: 1239px) {
      .topics__item {
        font-weight: bold;
        font-size: 20px;
        padding: 16px 0; } }

.questions {
  height: 81.3vh; }
  .questions__wrap {
    position: relative;
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(7, 1fr);
    height: 81.3vh;
    z-index: 5; }
  .questions__bg {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: -1; }
  .questions__item {
    text-align: center;
    border: 1px solid #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #0cc2f0; }
    .questions__item--red {
      background-color: #f57070; }
    .questions__item--orange {
      background-color: #fab025; }
    .questions__item--yellow {
      background-color: #fafa2c; }
    .questions__item--green {
      background-color: #27f327; }
    .questions__item--lblue {
      background-color: #67d7f3; }
    .questions__item--blue {
      background-color: #2338f7; }
    .questions__item--violet {
      background-color: #a10bf7; }
    .questions__item:hover {
      background-color: #b1c7c7; }
  .questions__link {
    width: 100%;
    padding: 30px 0;
    display: block;
    color: #6b5c5c;
    font-size: 50px;
    text-shadow: 2px 2px 3px #f5f3f3; }
    @media (max-width: 1239px) {
      .questions__link {
        font-size: 40px; } }

@keyframes rotate-colors {
  100% {
    filter: hue-rotate(360deg); } }

.open {
  background-color: transparent; }

.test__wrap {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-image: radial-gradient(#03fa18, #0cc2f2); }

.test__field {
  margin: 60px 0; }

.field {
  width: 80%;
  margin: 0 auto; }
  .field__title {
    border: 2px solid #f8eb7a;
    border-radius: 20px;
    padding: 30px 40px;
    color: #fff;
    font-size: 40px;
    text-shadow: 3px 3px 6px #302f2f;
    letter-spacing: 0.1em;
    background-color: #2846ee;
    line-height: 1em;
    box-shadow: 8px 8px 18px #000; }

.ansver__wrap {
  width: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr; }

.ansver__item {
  position: relative;
  border-radius: 20px;
  margin: 18px 10px;
  padding: 30px 20px 30px 70px;
  font-size: 40px;
  color: #fff;
  letter-spacing: 0.1em;
  line-height: 1em;
  text-shadow: 3px 3px 6px #302f2f;
  background-color: #2846ee;
  box-shadow: 8px 8px 18px #000;
  text-align: left; }
  .ansver__item:active {
    box-shadow: 1px 1px 4px 0 #000; }

.ansver__span {
  position: absolute;
  left: 20px;
  color: #eeb437;
  top: 50%;
  transform: translateY(-50%); }

.blue {
  background-color: #2846ee; }

body {
  font-family: sans-serif; }

img {
  max-width: 100%; }

.flex {
  display: flex;
  justify-content: space-between;
  align-items: center; }
